/* login container */
.login-container {
    width: 30%;
    height: auto;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: rgba(17, 17, 26, 0.1) 0px 4px 16px, rgba(17, 17, 26, 0.05) 0px 8px 32px;
    padding-bottom: 50px;
    border-radius: 10px;
}

/* form */
.login-container form {
    width: 50%;
}

/* label */
.login-container label {
    display: block;
    margin: 15px auto;
}

/* label text */
.login-container span {
    display: block;
    margin-bottom: 4px;
}

/* input field */
.login-container input {
    width: 100%;
    height: 30px;
    border-radius: 4px;
    border: none;
    outline: none;
}

/* Demo credentials */
.demo-container {
    display: flex;
    align-items: center;
    gap: 10px;
    margin: 15px 0 0 0;
    padding: 0;
}

.demo-name {
    margin: 0;
    padding: 0;
}

.show-icon, .hide-icon {
    width: 20px;
    height: auto;
    margin: 1.5px 0 0 0;
    padding: 0;
    cursor: pointer;
}

.credentials-container {
    margin: 10px 0 0 0;
    padding: 10px;
    display: flex;
    flex-direction: column;
    background-color: rgba(0, 0, 0, 0.5);
    border-radius: 5px;
}

.email-credential, .password-credential {
    margin: 0;
    padding: 0;
}

.hidden {
    display: none;
}

/* media queries */
@media (max-width: 1550px) {
    .login-container {
        width: 40%;
    }
}

@media (max-width: 1200px) {
    .login-container {
        width: 50%;
    }
}

@media (max-width: 950px) {
    .login-container {
        width: 60%;
    }
}

@media (max-width: 750px) {
    .login-container {
        width: 80%;
    }
}

@media (max-width: 550px) {
    .login-container {
        width: 95%;
    }
}

@media (max-width: 500px) {
    .login-container form {
        width: 80%;
    }
}